<template>
  <div class="page-404">
    <div class="page-404__header">
      <the-logo-inner/>
    </div>

    <div class="page-404__content">
      <div class="page-404__content-text">
        <div class="page-404__content-heading">
          Page not found...
        </div>
        <div class="page-404__content-description">
          We are unable to find the page you are looking for
        </div>
        <div class="page-404__content-controls">
          <e-button
            fill="green"
            size="default"
            class="page-404__button"
          >
            <router-link :to="{ name: 'index' }" class="page-404__button-link">
              Go to homepage
            </router-link>
          </e-button>
        </div>
      </div>

      <div class="page-404__content-illustration">
        <e-icon :src="iconRobot"/>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Page: Страница 404
 *
 * @version 1.0.0
 */

import { routesMixin } from '@/util/mixins';
import { mapGetters } from 'vuex';

// Components
import TheLogoInner from '@/containers/blocks/TheLogoInner';
import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';

// Icons
import iconRobot from '@/icons/e-icon_robot.svg';

export default {
  name: 'page-404',

  data() {
    return {
      iconRobot,
    };
  },

  computed: {
    ...mapGetters({
      firstContractHash: 'futuresActiveContract/firstContractHash',
    }),
  },


  components: {
    TheLogoInner,
    EIcon,
    EButton,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-404 {
  $this: &;

  background: #fff;
  min-height: 100vh;

  &__header {
    padding: 20px 40px 0 20px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include r(860) {
      flex-direction: column;
      align-items: center;
      padding: 20px 0 40px;
    }
  }

  &__header-menu {
    display: flex;
    align-items: center;

    @include r(860) {
      margin-left: 40px;
    }

    @include r(650) {
      margin-left: 0;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__header-menu-item {
    display: flex;
    align-items: center;
    text-decoration: none;
  }

  &__header-menu-item-connect {
    @include r(650) {
      flex: 0 0 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 12px 0 0;
    }
  }


  &__header-menu-item-link {
    display: block;
    padding: 0 20px;
    color: #09168A;
    opacity: .5;
    transition: opacity .15s ease-in-out;
    font-size: 14px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      opacity: 1;
    }

    @include r(1100) {
      font-size: 13px;
      padding: 0 12px;
    }

    @include r(480) {
      font-size: 13px;
      padding: 0 8px;
    }

    @include r(450) {
      font-size: 12px;
      padding: 0 8px;
    }

    @include r(400) {
      font-size: 11px;
      padding: 0 6px;
    }
  }

  &__header-menu-num {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    background: #F3376C;
    font-size: 12px;
    color: #fff;
    border-radius: 50%;
    margin-left: -12px;
    margin-right: 12px;
  }

  &__header-menu-button {
    background: #2EEBA9;
    border-radius: 40px;
    font-size: 14px;
    font-weight: 700;
    padding: 0 18px;
    height: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    transition: $transition-time;
    transform: scale(1) perspective(1px);
    margin-left: 24px;
    border: 1px solid transparent;
    cursor: pointer;

    &:hover {
      background: #1FCE43;
      transform: scale(1.04);
      border-color: #008D00;
    }

    @include r(650) {
      margin-left: 0;
    }
  }

  &__content {
    width: 970px;
    margin: 0 auto;
    padding: 0 40px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: calc(100vh - 180px);

    @include r(992) {
      width: auto;
      padding: 0 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__content-heading {
    font-size: 62px;
    color: #09168A;
    font-weight: 700;
    padding-bottom: 10px;

    @include r(992) {
      font-size: 50px;
      padding-bottom: 0;
    }

    @include r(470) {
      font-size: 42px;
      margin-bottom: -6px;
    }

    @include r(360) {
      font-size: 32px;
    }
  }

  &__content-text {
    @include r(992) {
      text-align: center;
    }
  }

  &__content-description {
    color: #09168A;
    opacity: .4;
    font-size: 19px;
    padding-bottom: 36px;

    @include r(470) {
      font-size: 18px;
      padding-bottom: 24px;
    }
  }

  &__content-controls {
    display: flex;

    @include r(992) {
      justify-content: center;
    }
  }

  /*&__button {*/
    /*border-radius: 40px;*/
    /*font-size: 14px;*/
    /*font-weight: 700;*/
    /*padding: 0 44px;*/
    /*height: 61px;*/
    /*display: flex;*/
    /*align-items: center;*/
    /*justify-content: center;*/
    /*color: #fff;*/
    /*text-decoration: none;*/
    /*text-transform: uppercase;*/
    /*transition: background-color .15s ease-in-out;*/
    /*margin-right: 24px;*/

    /*&.green {*/
      /*background: #1FCE43;*/
      /*box-shadow: 0 5px 9px 0 rgba(19,126,41, .11);*/
    /*}*/

    /*&.blue {*/
      /*background: #0123E7;*/
      /*box-shadow: 0 5px 9px 0 rgba(1,35,231, .19);*/
    /*}*/
  /*}*/

  &__button.e-button_padding_default {
    padding: 0;
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__button-link {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: #fff;
    padding: 0 40px;
    box-sizing: border-box;
    position: relative;
    z-index: 2;

    @include r(500) {
      padding: 0;
      width: 148px;
    }
    @include r(360) {
      padding: 0 15px;
      width: 140px;
    }
  }

  &__content-illustration {
    width: 322px;
    height: 361px;
  }

  .the-logo-inner {
    @include r(650) {
      padding-left: 0 !important;
    }
  }

  .the-logo-inner__dmex svg path:nth-child(2) {
    fill: #000;
  }
}
</style>



